import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { ManagedClient } from '../../shared/models/managed-client';
import { Assignment } from '../../shared/models/Assignment';
import { AssessmentGroup } from 'app/shared/models/assessment-group';
import { ClientService } from '../../shared/services/client.service';
import { Assessment } from "../../shared/models/assessment";
import { Location } from "@angular/common";
import { DacConfig } from "../../shared/services/dac-config.service";
import { AssessmentTokenService } from "../../shared/services/assessment-token.service";
import { ReportService } from "../../reports/services/report.service";
import { AlertService } from 'app/shared/services/alert.service';
import { ShoppingService } from 'app/shared/services/shopping.service';
import { AssessmentAssignerService } from '../services/assessment-assigner.service';
import { AssignmentService } from 'app/shared/services/assignment.service';
import { LibraryAsset } from 'app/library-assets/models/library-asset';
import { AssignmentSingle } from 'app/shared/models/AssignmentSingle';
import { AssignedLibraryAsset } from 'app/shared/models/AssignedLibraryAsset';
import { QuestionnaireService } from 'app/shared/services/questionnaire.service';
import { AssignedQuestionnaire } from 'app/shared/models/AssignedQuestionnaire';

@Component({
  selector: 'dac-client-manage-assignments',
  templateUrl: './client-manage-assignments.component.html',
  styleUrls: ['./client-manage-assignments.component.scss']
})
export class ClientManageAssignmentsComponent implements OnInit, OnChanges {

  @Input() client:ManagedClient;

  @Input() appointment:string;

  public isSaving:boolean;

  private clientAssignments:Assignment[];

  constructor(private _clientService: ClientService,
    private alertService: AlertService,
    private assessmentTokenService:AssessmentTokenService,
    private shoppingService:ShoppingService,
    private assessmentAssignerService:AssessmentAssignerService,
    private assignmentService:AssignmentService
    ) {
      this.isSaving = false;
    }

  ngOnInit() {
    this.updateClientAssignments();
  }
  ngOnChanges() {
    this.updateClientAssignments();
  }

  private updateClientAssignments() {
    if (this.client && this.appointment) {
      this.clientAssignments = this.client.assignments.filter(a => a.AppointmentId == this.appointment);
    } else if (this.client) {
      this.clientAssignments = this.client.assignments;
    }
  }

  public get assignments(): Assignment[] {
    return this.clientAssignments;
  }

  public async addAssessmentsFromBattery(group:AssessmentGroup) {
    try {
      // should we show the name of the client in the alerts?
      this.isSaving = true;
      this.alertService.info("Adding " + group.Name + " to " + this.client.getDisplayString(), 120000);
      let assignment = await this.assessmentAssignerService.addAssessmentsFromBattery(this.client, group, this.appointment);
      this.updateClientAssignments();
      this.alertService.success("Added " + assignment.Name + " to " + this.client.getDisplayString());
    }
    catch (error)
    {
      this.alertService.error("Failed to add assignment. Detailed error: " + (error.message || " no detailed error"), 20000);
      console.error(error);
    }
    finally {
      this.isSaving = false;
    }
  }

  public async addQuestionnaire(questionaire: any) {
    try {
      this.isSaving = true;
      let assignment = new AssignmentSingle();
      assignment.Name = questionaire.title;
      assignment.Type = "Questionnaire";
      assignment.DateAssigned = new Date();
      assignment.AppointmentId = this.appointment;
      let assignmentItem = new AssignedQuestionnaire();
      assignmentItem.Name = questionaire.title;
      assignmentItem.QuestionnaireId = questionaire.id;
      assignment.Items.push(assignmentItem);

    let alertId = this.alertService.info("Adding " + assignment.Name + " to " +this.client.getDisplayString(), 120000);
    this.assignmentService.saveClientAssignment(this.client, assignment)
      .then((assignment:Assignment) => {
        this.isSaving = false;
        this.client.assignments.push(assignment);
        this.updateClientAssignments();
        this._clientService.syncLocalClient(this.client);
        this.alertService.clearAlert(alertId);
        this.alertService.success("Added " + assignment.Name + " to " + this.client.getDisplayString());
      })
      .catch((error) => {
        this.isSaving = false;
        this.alertService.clearAlert(alertId);
        this.alertService.error("There was an error in adding this item to the client.  Please try again or contact customer support");
        console.error(error);
      });
      this.alertService.success("Added " + questionaire.title + " to " + this.client.getDisplayString());
  }
  catch (error)
  {
    this.alertService.error("Failed to add assignment. Detailed error: " + (error.message || " no detailed error"), 20000);
    console.error(error);
  }
  finally {
    this.isSaving = false;
  }
  }

  public async addAssessment(assessment: Assessment) {
    try {
        this.isSaving = true;
        this.assessmentAssignerService.addAssessment(this.client, assessment, this.appointment)
        .then(() => {
          this.updateClientAssignments();
        });
    }
    catch (error)
    {
      this.alertService.error("Failed to add assignment. Detailed error: " + (error.message || " no detailed error"), 20000);
      console.error(error);
    }
    finally {
      this.isSaving = false;
    }
  }

  public clearAssignment(assignment: Assignment) {
    this.isSaving = true;
    try {
      this.alertService.info("Removing assignment...");
      this.assignmentService.removeClientAssignment(this.client, assignment).then(() => {
        this.client.removeAssignment(assignment);
        this._clientService.syncLocalClient(this.client);
        this.updateClientAssignments();
        this.alertService.success("Assignment removed");
      })
      .catch((error) => {
        console.error(error);
        this.alertService.error("Failed to remove assignment", 1500);
      });
    }
    catch (error) {
      console.error(error);
          this.alertService.error("Failed to remove assignment", 1500);
    }
    finally {
      this.isSaving = false;
    }
  }

  public addAssetAssignment(asset:LibraryAsset) {

    if (!asset) {
      console.error("asset was null.  Cannot add");
      return;
    }
    this.isSaving = true;

    let assignment = new AssignmentSingle();
    assignment.Type = "LibraryAsset";
    assignment.DateAssigned = new Date();
    assignment.AppointmentId = this.appointment;
    let assignmentItem = new AssignedLibraryAsset();
    assignmentItem.Name = asset.title;
    assignmentItem.Type = "LibraryAsset";
    assignmentItem.AssetId = asset.id;
    assignment.Items.push(assignmentItem);

    let alertId = this.alertService.info("Adding " + assignment.Name + " to " +this.client.getDisplayString(), 120000);
    this.assignmentService.saveClientAssignment(this.client, assignment)
      .then((assignment:Assignment) => {
        this.isSaving = false;
        this.client.assignments.push(assignment);
        this.updateClientAssignments();
        this._clientService.syncLocalClient(this.client);
        this.alertService.clearAlert(alertId);
        this.alertService.success("Added " + assignment.Name + " to " + this.client.getDisplayString());
      })
      .catch((error) => {
        this.isSaving = false;
        this.alertService.clearAlert(alertId);
        this.alertService.error("There was an error in adding this item to the client.  Please try again or contact customer support");
        console.error(error);
      });

  }

  handleBatteryCheckout(group:AssessmentGroup) {
    let checkoutSubscription = null;
    let tokenService = this.assessmentTokenService;

    checkoutSubscription = this.shoppingService.getCheckoutStatus().subscribe((status) => {
      if (checkoutSubscription && (status == "ORDER_COMPLETE" || status == "ORDER_CANCELLED")) {
        checkoutSubscription.unsubscribe();
      }

      if (status == "ORDER_COMPLETE") {
        // we will refresh this when we the user's get back to the screen, but we need to add it at the time of purchase
        tokenService.addTokensForGroup(group, 1);
        this.addAssessmentsFromBattery(group);
      }
    });
  }

  handleAssessmentCheckout(assessment:Assessment  ) {
    let checkoutSubscription = null;
    let assignerService = this.assessmentAssignerService;
    let tokenService = this.assessmentTokenService;
    let client = this.client;

    checkoutSubscription = this.shoppingService.getCheckoutStatus().subscribe((status) => {
      if (checkoutSubscription && (status == "ORDER_COMPLETE" || status == "ORDER_CANCELLED")) {
        checkoutSubscription.unsubscribe();
      }

      if (status == "ORDER_COMPLETE") {
        // we will refresh this when we the user's get back to the screen, but we need to add it at the time of purchase
        tokenService.addTokens(assessment, 1);
        this.addAssessment(assessment);
      }
    });
  }

}
