import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { FhirClientComponent } from "app/debug/fhir-client/fhir-client.component";
import { Assignment } from "app/shared/models/Assignment";
import { ManagedClient } from "app/shared/models/managed-client";
import { ClientService } from "app/shared/services/client.service";
import { FhirService } from "app/shared/services/fhir-service";

