@Component(
  {
    templateUrl: './client-appointment-assignment-edit.component.html',
    selector: 'dac-client-appointment-assignment-edit'
  }
)
export class ClientAppointmentAssignmentEdit implements OnInit {
  public client:ManagedClient;
  public appointment:any;
  public assignments:Assignment[];

  constructor(private fhirClient:FhirService, private clientService:ClientService
    , private route:ActivatedRoute) {

  }
  ngOnInit(): void {
    let clientId = this.route.snapshot.params['id'];
    let clientPromise = this.clientService.getClient(clientId)
    let fhirClientPromise = this.fhirClient.getFhirClient();
    Promise.all([clientPromise, fhirClientPromise])
    .then(results => {
      this.client = results[0];
      let fhirClient = results[1];
      let appt = this.fhirClient.getFhirContextFromClient(fhirClient, 'Appointment');
      if (appt && appt.reference && appt.reference.indexOf("/") !== false) {
        let parts = appt.reference.split("/");
        let id = parts[1];
        this.appointment = id;
        this.assignments = this.client.assignments.filter(a => a.AppointmentId == id);
      }
    });
  }

}
