export const adminRoutes: Routes = [{
    path: '',
    component: AdminComponent,
    canActivate: [],
    children: [
      {
        path: '',
        children: [
          { path: 'client', children: [
            { path: 'new',  component: ClientNewComponent }
            ,{ path: ':id',  children: [
                {path: '', component: ClientEditComponent }
                ,{path: 'result/:resultId', component: AssessmentResultViewComponent }
                ,{path: 'asset-result/:resultId', component: AssetResultViewComponent }
                ,{path: 'questionnaire-result/:resultId', component: QuestionnaireResultViewComponent }
                ,{path: 'report/:reportId/:assignmentId', component: ReportViewerComponent }
                ,{path: 'embed', component: ClientEmbeddedViewComponent, children: [
                  {path: 'result/:resultId', component: AssessmentResultViewComponent }
                  ,{path: 'asset-result/:resultId', component: AssetResultViewComponent }
                  ,{path: 'report/:reportId/:assignmentId', component: ReportViewerComponent }
                  ,{path: 'questionnaire-result/:resultId', component: QuestionnaireResultViewComponent }
                ]}
            ]}
            ,{ path: '', component: ClientListComponent}
          ]}
          ,{path: 'client-appointment/:id', component:ClientAppointmentAssignmentEdit }
          ,{path: 'users', 'loadChildren': () => import('../users/users.module').then(m => m.UsersModule)}
          ,{path: 'companies', loadChildren: () => import('../companies-admin/companies-admin.module').then(m => m.CompaniesAdminModule) }
          ,{path: 'system', loadChildren: () => import('./system-overview/system-overview.module').then(m => m.SystemOverviewModule)}

          // TODO: stephen would this be better as a child component of client?

          // ,{ path: 'shop', component: AdminShopComponent }
          ,{ path: 'shop', children: [
            { path: '', component: ShopItemsComponent }
            ,{ path: 'checkout/thankyou', component: CheckoutThankyouComponent }
            ,{ path: 'checkout', component: CheckoutComponent }
          ]}


          ,{ path: 'assessment', component: AssessmentAdminComponent,
            children: [
              {path: 'batteries', component: AssessmentGroupEditComponent}
              ,{path: 'custom-reports', loadChildren: () => import('../manage-reports/manage-reports.module').then(m => m.ManageReportsModule) }
              ,{path: 'manage', children: [
                { path: '',component: AssessmentListComponent}
                ,{ path: 'new', component: AssessmentNewComponent }
                ,{ path: ':uid/view', component: AssessmentViewComponent, children: [
                  { path: '', redirectTo: 'details', pathMatch: 'prefix' }
                  ,{ path: 'details', component: AssessmentDetailsViewComponent }
                  ,{ path: 'scales', component: ScalesListViewComponent }
                  ,{ path: 'sets', component: QuestionSetListViewComponent }
                ] }
                ,{ path: ':uid/edit', component: AssessmentEditComponent, children: [] }
                ,{ path: ':uid/edit/question-set', children: [
                  { path: ':setId/edit', component: QuestionSetEditComponent }
                  ,{ path: 'new', component: QuestionSetNewComponent }
                ] }
                // even though it seems question is a sub of question-set we have no router outlet
                // for it... we want it to replace the entire screen... so... we do this.
                ,{ path: ':uid/edit/question-set/:setId/edit/question', children: [
                    {path: 'new', component: QuestionNewComponent}
                    ,{path: ':questionId/edit', component: QuestionEditComponent}
                  ] }
                ,{ path: ':uid/edit/question-set/:setId/edit/label', children: [
                    {path: 'new', component: LabelNewComponent}
                    ,{path: ':labelId/edit', component: LabelEditComponent}
                  ] }
                ,{ path: ':uid/edit/scale', children: [
                  { path: ':scaleId/edit', component: ScaleEditComponent }
                  ,{ path: 'new', component: ScaleNewComponent }
                ] }
                ,{ path: ':uid/edit/scale/:scaleId/edit/scale-range', children: [
                    {path: 'new', component: ScaleRangeNewComponent}
                    ,{path: ':rangeId/edit', component: ScaleRangeEditComponent}
                  ] }
              ]}
            ]
          }

          // library assets
          , { path: 'library/:type', component: ExerciseListComponent, children: [] }
          , { path: 'library/:type/new', component: AssetNewComponent, children: [] }
          , { path: 'library/:type/:assetId', component: AdminViewAssetComponent, children: [] }

          ,{ path: 'my-account', component: MyAccountComponent, children: [
            { path: 'order', component: OrderHistoryComponent }
            , { path: 'order/:id', component: ViewOrderPageComponent}
            ,{ path: 'billing', component: BillingDetailsComponent }
            ,{ path: 'plans', component: BillingPlansComponent }
            ,{ path: 'company', component: AccountOwnerSettingsComponent }
            , { path: '', pathMatch: 'full', redirectTo: '/admin/my-account/order' }
          ]}
          ,{ path: '**', redirectTo: '/std/admin/client', pathMatch: 'full'}

 //

  // { path: 'assessments/:uid/edit/scale/new', component: AssessmentScaleNewComponent },
  // { path: 'assessments/:uid/edit/scale/:scaleId/edit', component: AssessmentScaleEditComponent },

  // { path: 'assessments/:uid/edit/scale/:scaleId/edit/scale-range/new', component: AssessmentScaleRangeNewComponent },
  // { path: 'assessments/:uid/edit/scale/:scaleId/edit/scale-range/:rangeId/edit', component: AssessmentScaleRangeEditComponent },

  // { path: 'assessments/:uid/edit/questionSet/new', component: QuestionSetNewComponent },
  // { path: 'assessments/:uid/edit/questionSet/:setId/edit', component: QuestionSetEditComponent },

  // { path: 'assessments/:uid/edit/questionSet/:setId/edit/question/new', QuestionNewComponent  },
  // { path: 'assessments/:uid/edit/questionSet/:setId/edit/question/:questionId', QuestionEditComponent  },

  // { path: 'assessments/:uid/edit/questionSet/:setId/edit/label/new', QuestionSetLabelNewComponent  },
  // { path: 'assessments/:uid/edit/questionSet/:setId/edit/label/:labelId', QuestionSetLabelEditComponent  },

        ]
    }
  ]
}];
