import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ActivatedRoute,Router } from '@angular/router';
import {Modal} from "ngx-modal";
import { SubscriptionLike as ISubscription } from 'rxjs';
import { SubscriptionService } from '../services/subscription.service';

@Component({
  selector: 'dac-subscription-dialog',
  templateUrl: './subscription-dialog.component.html',
  styleUrls: ['./subscription-dialog.component.scss']
})
export class SubscriptionDialogComponent {

  constructor(
    private subscriptionService:SubscriptionService
    ,private router:Router
    ,private dialogRef:MatDialogRef<SubscriptionDialogComponent>
  ) {
    console.log("got here");
  }

  public close() {
    this.closeDialog();
  }

  public goToSubscriptionSignup() {
    let pageRoute = this.subscriptionService.getSubscriptionPageRoute();
    this.router.navigate(pageRoute).then(() => {
      this.closeDialog();
    });
  }

  private closeDialog() {
    this.dialogRef.close();
  }
}
