import { Component, HostBinding, OnDestroy, OnInit } from '@angular/core';
import { NavBarService } from 'app/theme/services/nav-bar.service';

import { DacConfig } from "../../shared/services/dac-config.service";
import { SubscriptionService } from '../services/subscription.service';
import { SubscriptionLike as ISubscription } from 'rxjs';
import { ModalService } from 'app/shared/services/modal.service';
import { SubscriptionDialogComponent } from '../subscription-dialog/subscription-dialog.component';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit, OnDestroy {

  private _subscriptionServiceSubscription:ISubscription;

  constructor(
    private dacConfig:DacConfig
    ,private navService:NavBarService
    ,private subscriptionService:SubscriptionService
    ,private modalService:ModalService
  ) {
  }

  ngOnInit() {
    // turn it on
    this.navService.toggleSidebar();
    this._subscriptionServiceSubscription = this.subscriptionService.subscriptionRequiredEvent().subscribe(() => {
      this.openSubscriptionDialog();
    });
  }

  private openSubscriptionDialog() {
    this.modalService.openDialog(SubscriptionDialogComponent);
  }

  ngOnDestroy() {
    // turn it off
    this.navService.toggleSidebar();
    if (this._subscriptionServiceSubscription) {
      this._subscriptionServiceSubscription.unsubscribe();
      this._subscriptionServiceSubscription = null;
    }
  }

  get canManageAssessments() {
    return this.dacConfig.isFeatureEnabled("admin.assessments.navigation");
  }

  get shouldShowAddoHome() {
    return this.dacConfig.isFeatureEnabled("addo.dashboard.admin");
  }

  get canShop() {
    return this.dacConfig.isFeatureEnabled("admin.shop");
  }

  goToAddoHome(event:Event) {
    event.preventDefault();
    event.stopPropagation();

    // go to the base url
    window.location.href = "/";
    return false;
  }
}
